function whiteLabel() {
  // Wait until app is done loading
  waitForElement("#app:not(.loading)").then((el) => {
    // log("App Loaded according to selector (#app:not(.loading)");
    let faviconUrl = "";
    var link =
      document.querySelector("link[rel*='icon']") ||
      document.createElement("link");
    faviconUrl =
      "https://storage.googleapis.com/msgsndr/WQZjWNMM1muqdOrhPcO3/media/639781afa4b006149a9326de.png";
    // Upload an image into a location funnel and then use the URL here.

    link.type = "image/x-icon";
    link.rel = "shortcut icon";
    link.href = faviconUrl;
    document.getElementsByTagName("head")[0].appendChild(link);
  });
}

