// React
import React from "react";
import ReactDOM from "react-dom/client";
// Redux
import { Provider } from "react-redux";
import { store } from "../../redux/store";
// Helpers
import log from "../../helpers/log";
import waitForElement from "../../helpers/waitForElement";
import SupportButton from "./support-button";

function embedSupportButton() {
  if (!document.querySelector("#img_nav_root")) {
    let avatar = document.querySelector(".hl_header--avatar");
    let container = document.createElement("div");
    container.id = "img_nav_root";
    avatar.parentElement.insertAdjacentElement("beforebegin", container);
    const root = ReactDOM.createRoot(document.getElementById("img_nav_root"));

    root.render(
      <React.StrictMode>
        <Provider store={store}>
          <SupportButton />
        </Provider>
      </React.StrictMode>
    );
  }
}

export default embedSupportButton;
