import waitForElement from "./helpers/waitForElement";
import log from "./helpers/log";

// log("VAR", __CLIENT_ID__);
// log("Adding event listeners.");
// This is from the Panel
import { store } from "./redux/store";
import { toggle } from "./components/chat-widget/chat-widget-slice";

const __CLIENT_ID__ = document.getElementById("img_plugin").dataset.clientId;

// Inbound
window.addEventListener(
  "message",
  (event) => {
    // Do we trust the sender of this message?  (might be
    // different from what we originally opened, for example).
    // log("Message received");
    if (
      !(
        event.origin === "https://panel.imgfunnels.com" ||
        (process.env.NODE_ENV === "development" &&
          event.origin === "http://localhost:3000")
      )
    )
      return;

    if (event.data.message) {
      // console.log("Message received", event.data.message);
      switch (event.data.message) {
        case "Open chat widget":
          document.getElementById("img-sidebar").classList.add("open");
          store.dispatch(toggle());
          setTimeout(() => {
            window.leadConnector.chatWidget.openWidget();
          }, 500);

          break;
        case "Send routeChange event":
          const routeChange = new CustomEvent("routeChangeEvent", {
            detail: {
              to: {
                name: "dashboard-v2",
                params: { location_id: "WQZjWNMM1muqdOrhPcO3" }
              }
            }
          });
          console.log("IMG Route Change Event");
          window.dispatchEvent(routeChange);
          break;
        default:
          console.log("Message received but fell through switch");
          break;
      }
    }
  },
  false
);

// Outbound
waitForElement(".hl_wrapper iframe").then(async (iframe) => {
  let COOKIE = JSON.parse(window.localStorage.getItem("img_data"));
  log("COOKIE", COOKIE);
  log("STATE (GLOBAL VAR) 1", window.img_state);
  log("STATE (GLOBAL VAR) 2", window?.app?.__vue__?.$store?.state);

  // Fixes an issue where elements inside of CustomJS (roots) are hidden
  waitForElement("#customJS").then((el) => {
    if (el.style.display === "none") {
      el.style.display = "block";
    }
  });

  let serializedState = JSON.stringify(window?.app?.__vue__?.$store?.state);
  if (iframe) {
    iframe.contentWindow.postMessage({ serializedState }, "*");
    iframe.contentWindow.postMessage({ __CLIENT_ID__ }, "*");
  }
});
