import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import { toggle } from "./chat-widget-slice";

import { v4 } from "uuid";

import styled from "styled-components";
import { Close } from "@styled-icons/remix-line/Close";
import { ChevronUp } from "@styled-icons/octicons/ChevronUp";

import { EmojiSmile } from "@styled-icons/bootstrap/EmojiSmile";
import { Paperclip } from "@styled-icons/bootstrap/Paperclip";
import { FileArrowUp } from "@styled-icons/bootstrap/FileArrowUp";
import { QuestionAnswer } from "@styled-icons/remix-line/QuestionAnswer";

const ChatWidget = () => {
  const open = useSelector((state) => state.chatWidget.open);
  const dispatch = useDispatch();
  const [waiting, setWaiting] = useState(true);

  const [chatWidgetFile, setChatWidgetFile] = useState(void 0);
  const [attachments, setAttachments] = useState([]);
  const [chatWidgetFileName, setChatWidgetFileName] = useState("");
  const [textAreaValue, setTextAreaValue] = useState("");
  const [startTime, setStartTime] = useState(Date.now());

  const [conversation, setConversation] = useState([]);
  const [effectToggle, setEffectToggle] = useState(false);

  const [_currentWebsocket, set_CurrentWebsocket] = useState(null);
  const [wroteWelcomeMessages, setWroteWelcomeMessages] = useState(false);
  // let LET_currentWebsocket = null;

  function addChatMessage(from, text) {
    console.log("DEBUG 1: Message From:", from, " | ", text);
    console.log("DEBUG 1: Websocket:", _currentWebsocket);

    if (chatWidgetFile) {
      setConversation((prevState) => {
        let filePath = URL.createObjectURL(chatWidgetFile);
        return [
          ...prevState,
          {
            from,
            el: (
              <p key={v4()} className={`from-${from}`}>
                <img src={filePath} className="fill" />
                <span>{text}</span>
              </p>
            )
          }
        ];
      });
      setAttachments((prevState) => [...prevState, chatWidgetFile]);
      setChatWidgetFile(void 0);
      setChatWidgetFileName("");
    } else if (text !== "") {
      setConversation((prevState) => {
        return [
          ...prevState,
          {
            from,
            el: (
              <p key={v4()} className={`from-${from}`}>
                {text}
              </p>
            )
          }
        ];
      });
    }

    if (_currentWebsocket) {
      _currentWebsocket.send(JSON.stringify({ message: textAreaValue }));
    } else {
      console.log("Message was sent without a current websocket.");
    }

    setTimeout(() => {
      var objDiv = document.getElementById("conversation-area");
      objDiv.scrollTop = objDiv.scrollHeight;
    }, 50);
  }

  useEffect(() => {
    let currentWebsocket = null;

    console.log("CHAT WIDGET MAIN EFFECT WAS TRIGGERED");

    setStartTime(Date.now());
    // console.log("Initializing Chat Widget", window.app);

    if (!window.app?.__vue__) {
      window.app = { __vue__: { $store: { state: {} } } };
      // TODO: Do not expose state (it contains the private Mailgun API key)
    }

    let __VUE_STATE__ = window?.app?.__vue__?.$store?.state;

    let chatlog = document.querySelector("#conversation-area");

    let hostname =
      process.env.NODE_ENV === "development"
        ? "0.0.0.0:8080"
        : "img-funnels-webhook.donaldwaynemoorejr.workers.dev";

    // console.log("HOSTNAME", hostname);
    if (hostname == "") {
      // Probably testing the HTML locally.
      hostname = "edge-chat-demo.cloudflareworkers.com";
    }

    let roomname = "test-x";

    let protocol = document.location.protocol;
    // If we are running via wrangler dev, use ws:
    const wss = protocol === "http:" ? "ws://" : "wss://";

    // console.log(
    //   "WEBSOCKET URL",
    //   wss + hostname + "/api/room/" + roomname + "/websocket"
    // );
    let ws = new WebSocket(
      wss + hostname + "/api/room/" + roomname + "/websocket"
    );

    currentWebsocket = ws;

    // let ws = new WebSocket(
    //   `${wss}${
    //     protocol === "http:"
    //       ? hostname
    //       : "img-funnels-webhook.donaldwaynemoorejr.workers.dev"
    //   }/api/room/${}/websocket`
    // );

    console.log("WEBSOCKET IS SET. OPENING...", currentWebsocket);

    console.log("SETTING HANDLERS!!!!", currentWebsocket);

    let lastSeenTimestamp = 0;

    if (!currentWebsocket) console.log("SET HANDLERS WITHOUT WS");

    currentWebsocket.addEventListener("open", (event) => {
      if (!currentWebsocket) {
        console.log("Websocket was cleaned up by React before it could open");
      } else {
        if (currentWebsocket.readyState === 1) {
          console.log("CONNECTED!", event, currentWebsocket);
          setWaiting(false);
          set_CurrentWebsocket(currentWebsocket);

          // addChatMessage("system", "Connected!");
          if (!wroteWelcomeMessages) {
            setConversation((prevState) => {
              return [
                ...prevState,
                {
                  from: "them",
                  el: (
                    <>
                      <p key={v4()} className={`from-them`}>
                        Welcome to IMG Funnels Customer Service.
                      </p>
                      <p key={v4()} className={`from-them`}>
                        How can I help you?
                      </p>
                    </>
                  )
                }
              ];
            });

            setWroteWelcomeMessages(true);
          }

          if (!currentWebsocket)
            console.error(
              "Tried to send message after websocket opened, but it was cleared before the callback was called."
            );

          console.log(
            "WEBSOCKET OPEN: ",
            currentWebsocket.readyState,
            " | ",
            currentWebsocket,
            new Date().toLocaleString()
          );

          // Send user info message.
          currentWebsocket.send(JSON.stringify({ name: "user" }));
        }
      }
    });

    currentWebsocket.addEventListener("message", (event) => {
      let data = JSON.parse(event.data);
      // debugger;

      console.log(
        "WEBSOCKET MSG: ",
        currentWebsocket.readyState,
        " | ",
        currentWebsocket,
        new Date().toLocaleString()
      );
      if (data.error) {
        addChatMessage("Error", "Error: " + data.error);
      } else if (data.joined) {
        // If this is the customer support agent, add the next line.
        // addChatMessage("Joined", data.joined + " joined");
      } else if (data.quit) {
        // If this is the customer support agent, add the next line.
        // addChatMessage("Quit", data.quit + " left chat");
        // If message is like { quit: "message" }
      } else if (data.ready) {
        console.log("DATA IS READY", data);
        // All pre-join messages have been delivered.
      } else {
        // A regular chat message.
        if (data.timestamp > lastSeenTimestamp) {
          if (data.name !== "user") {
            console.log("INBOUND MESSAGE:", data);
            addChatMessage("them", data.message);
          } else {
            console.log("OUTBOUND MESSAGE:", data);
            lastSeenTimestamp = data.timestamp;
          }
        }
      }
    });

    currentWebsocket.addEventListener("close", (event) => {
      console.log(
        "WEBSOCKET CLOSE: ",
        currentWebsocket.readyState,
        " | ",
        currentWebsocket,
        new Date().toLocaleString()
      );
      rejoin();
      // console.log(
      //   "Unhandled Event: Websocket closed.:",
      //   event.code,
      //   event.reason
      // );
    });
    currentWebsocket.addEventListener("error", (event) => {
      console.log(
        "WEBSOCKET ERROR: ",
        currentWebsocket.readyState,
        " | ",
        currentWebsocket,
        new Date().toLocaleString()
      );
      rejoin();
      // console.error("Unhandled Event: Websocket error.:", event);
    });

    let rejoin = async () => {
      set_CurrentWebsocket(null);
      // Don't try to reconnect too rapidly.
      let timeSinceLastJoin = Date.now() - startTime;
      if (timeSinceLastJoin < 10000) {
        // Less than 10 seconds elapsed since last join. Pause a bit.
        await new Promise((resolve) =>
          setTimeout(resolve, 10000 - timeSinceLastJoin)
        );
      }

      // OK, reconnect now!
      setEffectToggle(!effectToggle);
    };

    // Is the chatlog scrolled to the bottom?
    let isAtBottom = true;

    chatlog.addEventListener("scroll", (event) => {
      isAtBottom =
        chatlog.scrollTop + chatlog.clientHeight >= chatlog.scrollHeight;
    });
    // Detect mobile keyboard appearing and disappearing, and adjust the scroll as appropriate.
    if ("visualViewport" in window) {
      window.visualViewport.addEventListener("resize", function (event) {
        if (isAtBottom) {
          chatlog.scrollBy(0, 1e8);
        }
      });
    }

    return () => {
      console.log("CLEANING UP CURRENT WEBSOCKET");
      currentWebsocket = null;
      set_CurrentWebsocket(null);
    };
  }, [effectToggle]);

  return (
    <div
      id="img-chat-widget"
      className={`${
        open ? "open" : process.env.NODE_ENV === "development" ? "" : ""
      }`}
    >
      {/* <div className="fixed bottom-0 right-96 mr-28">
        DEBUGGER
      </div> */}
      <div>
        <div className="relative bg-gradient-to-br from-slate-100 to-white">
          <button
            onClick={() => {
              dispatch(toggle());
            }}
            className="absolute flex w-full h-full justify-start items-center pl-3"
          >
            {open ? <CloseIcon /> : <ChevronUpIcon />}
          </button>
          <div
            className="px-4 text-center text-slate-900"
            style={{
              fontSize: "1.1rem",
              paddingTop: "7.5px",
              paddingBottom: "7.5px",
              fontFamily: "SanFrancisco"
            }}
          >
            <QuestionAnswerIcon />{" "}
            <span style={{ minHeight: "22px", minWidth: "132.58px" }}>
              Support
            </span>
          </div>
        </div>
        {/* <div>Profile</div> */}

        {/* <iframe
          src="https://edge-chat-demo.donaldwaynemoorejr.workers.dev/"
          height="360"
          width="320"
        /> */}
        <div
          id="conversation-area"
          className="bg-white overflow-auto relative h-full"
          style={{ maxHeight: "377px", height: "377px" }}
        >
          <div className="imessage min-h-full">
            {waiting ? (
              <div className="typing-indicator mt-3">
                <span></span>
                <span></span>
                <span></span>
              </div>
            ) : (
              // <p id="chat-widget-waiting" className="from-system">
              //   ...
              // </p>
              ""
            )}

            {conversation.map((message) => (
              <div key={v4()}>message.el</div>
            ))}

            <div className="">{`{{user.first_name}}`} is typing...</div>
          </div>
        </div>
        <div className="absolute bottom-0 h-fit border-t w-full">
          <div className="flex">
            <input
              placeholder="Type your question here."
              id="img-chat-widget-input"
              className="w-full resize-none border-none active:outline-none focus:outline-none px-3 py-2 min-h-fit focus:ring-0 active:ring-0"
              value={textAreaValue}
              onChange={(e) => {
                setTextAreaValue(e.target.value);
              }}
              onKeyDown={(event) => {
                if (event.key === "Enter") {
                  event.preventDefault();
                  document.getElementById("chat-widget-submit-btn").click();
                }
              }}
            />
            {chatWidgetFileName ? (
              <div
                id="file-area"
                style={{ maxWidth: "108.5px", width: "108.5px" }}
                className="flex items-center justify-center flex-col px-3 border-l bg-white"
              >
                <FileArrowUpIcon />
                <div
                  className="truncate text-ellipsis whitespace-nowrap relative inline-block"
                  style={{ maxWidth: "76.5px" }}
                >
                  {chatWidgetFile.name}
                </div>
              </div>
            ) : (
              <></>
            )}
          </div>
          <div className="flex">
            <button
              className="flex items-center justify-center px-2 border border-r-0 hover:bg-neutral-50"
              onClick={() => {
                document.getElementById("img-chat-widget-file-upload").click();
              }}
            >
              <PaperclipIcon />
              <input
                type="file"
                accept="image/*"
                id="img-chat-widget-file-upload"
                className="hidden"
                value={chatWidgetFileName}
                onChange={(e) => {
                  if (e.target.files[0]) {
                    console.log(e.target.files[0]);
                    setChatWidgetFileName(e.target.value);
                    setChatWidgetFile(e.target.files[0]);
                  }
                }}
              />
            </button>
            <button
              id="chat-widget-submit-btn"
              onClick={() => {
                if (!_currentWebsocket)
                  console.log(
                    "Tried to send a message but there was no websocket connection (React.js / JSX)"
                  );

                addChatMessage("me", textAreaValue);
                setTextAreaValue("");
              }}
              className="w-full bg-blue-600 text-white py-2 border hover:bg-blue-600 text-lg"
            >
              Send
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ChatWidget;

const CloseIcon = styled(Close)`
  display: inline;
  width: 30px;
  margin-right: 5px;
  color: #0f172a;
`;

const ChevronUpIcon = styled(ChevronUp)`
  display: inline;
  width: 30px;
  margin-right: 5px;
  color: #0f172a;
`;

const QuestionAnswerIcon = styled(QuestionAnswer)`
  display: inline;
  width: 18px;
  margin-right: 5px;
`;

const PaperclipIcon = styled(Paperclip)`
  display: inline;
  width: 20px;
`;

const FileArrowUpIcon = styled(FileArrowUp)`
  display: inline;
  width: 30px;
`;
