const log = (msg, ...params) => {
  let styles =
    "color: #202020; font-family: 'serif', monospace; letter-spacing: 2px; font-weight: 800; background-image: linear-gradient(to bottom, orange, rebeccapurple); font-size: 17px; padding-left: 5px; padding-right: 5px;";
  let head = `%cimgFunnels\n`;
  if (params) {
    console.log(head, styles, msg, ...params);
  } else {
    console.log(head, styles, msg);
  }
};

export default log;
